<template>

    <div class="add-city">
      <div :class="{'error-field': error}">
        <input type="text" placeholder="Enter city name" v-model="cityName" @keyup.enter="addCity">
        <p class="error">This city was not found, please enter city name</p>
      </div>

      <button class="btn" type="button" @click="addCity">Add city</button>
    </div>

</template>

<script>
export default {
  data() {
    return {
      cityName: '',
      error: ''
    }
  },

  computed: {
    getError() {
      return this.$store.getters.getError
    },

    getForecasts() {
      return this.$store.getters.getForecasts
    },
  },

  methods: {
    addCity() {
      this.$store.dispatch('addForecasts', this.cityName)
      this.error = this.getError
      this.$store.commit('addError', this.error)
      this.cityName = ''
    }
  },

  watch: {
    getError() {
      this.error = this.getError
    }
  },
}
</script>
