<template>

    <div class="city-list">

      <transition name="slide" mode="out-in">
        <p class="city-loader slide-item" v-if="!forecast.id && forecasts.length === 0">Load cities ...</p>
      </transition>

      <transition name="slide" mode="out-in">
        <div class="city-item slide-item" v-if="forecast.id">
          <strong>
            {{forecast.name}}, {{forecast.sys.country}}
          </strong>
          <span class="note"> - current location</span>
          <ul>
            <li>temperature {{forecast.main.temp | mathRound}}<sup>o</sup> C</li>
            <li>wind speed {{forecast.wind.speed}} m/s</li>
          </ul>
          <div class="delete" @click="deleteCurrentPos">&times;</div>
        </div>
      </transition>

      <transition-group name="slide" tag="div" mode="out-in">
        <div class="city-item slide-item" v-for="(item, index) in forecasts" :key="item.name">
          <strong>{{item.name}}, {{item.sys.country}}</strong>
          <ul>
            <li>temperature {{item.main.temp | mathRound}}<sup>o</sup> C</li>
            <li>wind speed {{item.wind.speed}} m/s</li>
          </ul>
          <div class="delete" @click="deleteForecast(index)">&times;</div>
        </div>
      </transition-group>

    </div>

</template>

<script>

  export default {
    data() {
      return {
        forecast: {},
        forecasts: {}
      }
    },
    computed: {
      getForecast() {
        return this.$store.getters.getForecast
      },
      getForecasts() {
        return this.$store.getters.getForecasts
      }
    },
    methods:{
      deleteCurrentPos() {
        this.forecast = {}
      },
      deleteForecast(index) {
        this.forecasts.splice(index, 1)
        this.$store.commit('deleteForecast', this.forecasts)
      }
    },
    watch: {
      getForecast() {
        this.forecast = this.getForecast
      },
      getForecasts() {
        this.forecasts = this.getForecasts
      }
    },
    filters: {
      mathRound(value) {
        return value = parseInt(Math.round(value))
      }
    }
  }
</script>
