import axios from 'axios';
import store from '@/store'

export default {
  state: {
    forecasts: [],
    error: false
  },

  mutations: {
    addForecasts(state, payload) {
      let index = 0;
      state.forecasts.forEach( item => {
        if(payload.id === item.id) { index = 1; return }
      })
      if(index === 0) state.forecasts.unshift(payload)
      localStorage.setItem('forecasts', JSON.stringify(state.forecasts));
    },

    addError(state, payload) {
      state.error = payload
    },

    addForecastId(state, payload) {
      state.forecastId = payload
    },

    deleteForecast(state, payload) {
      state.forecasts.splice(payload, 0);
      localStorage.setItem('forecasts', JSON.stringify(state.forecasts));
    },

    loadForecasts(state) {
      state.forecasts = JSON.parse(localStorage.getItem('forecasts')) || [];
    },

  },

  actions: {
    addForecasts({commit, state}, cityName) {
      const URL = store.state.openWeatherUrl
                  + '?q=' + cityName
                  + '&units=metric&APPID='
                  + store.state.appId

      axios
        .get(URL)
        .then((response) => {

          commit('addForecasts', response.data);
          state.error = false
        })
        .catch((error) => {
          state.error = true
          console.log(error)
        });
    },


  },
  getters: {
    getForecasts(state) {
      return state.forecasts;
    },

    getError(state) {
      return state.error;
    },
  }

}
