import axios from 'axios';
import store from '@/store'

export default {
  state: {
    latitude: '',
    longitude: '',
    forecast: {}
  },

  mutations: {
    saveLatitude(state, payload) {
      state.latitude = payload;
    },

    saveLongitude(state, payload) {
      state.longitude = payload;
    },

    saveForecast(state, payload) {
      state.forecast = payload;
    },

  },

  actions: {
    saveLatitude(context, latitude) {
      context.commit('saveLatitude', latitude);
    },

    saveLongitude(context, longitude) {
      context.commit('saveLongitude', longitude);
    },
    
    saveCurrentPos({ commit, state }) {

      const URL = store.state.openWeatherUrl
                + '?lat=' + state.latitude
                + '&lon=' + state.longitude
                + '&units=metric&appid=' + store.state.appId
      axios
        .get(URL)
        .then((response) => {
            commit('saveForecast', response.data);
        })
        .catch((error) => {
          console.log(error);
        });
    }
  },

  getters: {
    getForecast(state) {
      return state.forecast;
    },
  }

}
